<template>
<q-page padding class="row justify ">
  <div class="doc-container">     
    <div class="text-center" >
     <div class="divOilfield" style="width:90vw; max-width: 70vw;"  >
         <div class="q-mb-md q-display-2 q-display-2-opacity">Формирование задания на строительство</div>
          <div class="row items-center" style="width:80vw; max-width: 90vw;">
  <div class="col-2 rows"><img id="img1" style="width:100px;height 120px " src="../assets/ImpPic1.png"></div>
  <div class="col-2"><h4>Блок электроснабжения</h4> </div>
  <div class="col-3"><p>Количество бригад    <q-chip square color="secondary">3</q-chip></p>
                  
                    </div>
  <div class="col-5">
  <p class="caption">
        Планируемое количество на будующий период
        <span class="chip-container">
          <q-chip square color="primary">
             {{ marker }}
          </q-chip>
        </span>
      </p>
      <q-slider v-model="marker" :min="0" :max="10" :step="2" label snap markers />

                  
  </div>
  
<div class="q-pt-lg q-pb-lg row items-center" style="width:90vw; max-width: 90vw;">
  <div class="col-2"></div>
  <div class="col-2"></div>
  <div class="col-3"><p>Стройматериалы    <q-chip square color="secondary">  67% </q-chip></p></div>
  <div class="col-5">
  <p >
        Планируемый объем на будующий период
        <span class="chip-container">
          <q-chip square color="primary">
             {{ marker1 }}%
          </q-chip>
        </span>
      </p>
      <q-slider v-model="marker1" :min="0" :max="100" :step="2" label snap markers />
        </div>
</div>

<div class="q-pt-lg q-pb-lg row items-center" style="width:80vw; max-width: 90vw;">
  <div class="col-2"></div>
  <div class="col-2"></div>
  <div class="col-3"><p>Объем финансирования    <q-chip square color="secondary">  82% </q-chip></p></div>
  <div class="col-5">
  <p >
        Планируемый объем на будующий период
        <span class="chip-container">
          <q-chip square color="primary">
             {{ marker2 }}%
          </q-chip>
        </span>
      </p>
      <q-slider v-model="marker2" :min="0" :max="100" :step="2" label snap markers />
  
  </div>
</div>


        </div>
    </div>     
    <div class="text-center q-pt-lg" >
     {{src}}

       <q-btn color="primary" @click="customDialogModel = true" label="Отправить задание" />

 <q-dialog
        v-model="customDialogModel"
        stack-buttons
        prevent-close
        @cancel="onCancel"
        @ok="onOk"
        @show="onShow"
        @hide="onHide"
      >
        <!-- This or use "title" prop on <q-dialog> -->
        <span slot="title">Favorite Superhero</span>

        <!-- This or use "message" prop on <q-dialog> -->
        <span slot="message">What is your superhero of choice?</span>

        <div slot="body">
          <q-field
            icon="account_circle"
            helper="We need your name so we can send you to the movies."
            label="Your name"
            :label-width="3"
            :error="nameError"
          >
            <q-input v-model="name" />
          </q-field>
        </div>

        <template slot="buttons" slot-scope="props">
          <q-btn color="primary" label="Choose Superman" @click="choose(props.ok, 'Superman')" />
          <q-btn color="black" label="Choose Batman" @click="choose(props.ok, 'Batman')" />
          <q-btn color="negative" label="Choose Spiderman" @click="choose(props.ok, 'Spiderman')" />
          <q-btn flat label="No thanks" @click="props.cancel" />
        </template>
      </q-dialog>

    </div>
</div>
            </div>
  </div>

</q-page>  
</template>
<script>
export default {
  data () {
    return {    
      option: 'opt1',
        marker: 6,
         marker1: 45,
         marker2: 37,
       src: '',
      customDialogModel: false,
        name: '',
      nameError: false
    }
  },
    methods: {
  copySrc: function(img){
            this.src = img.srcElement.src;
            },
            
    onOk () {
      console.log('ok')
    },
    onCancel () {
      console.log('cancel')
    },
    onShow () {
      console.log('show')
    },
    onHide () {
      console.log('hide')
    }
            }
}
</script>

<style lang="stylus">
@import '~variables'
.CaseDialog1
  padding-bottom 20px
  padding-left 0px
  font-style normal 
.CaseDialog2
  padding-bottom 20px
  padding-left 100px
  font-style italic
  font-weight bold

.CaseAnswerItem  
  font-style italic
  font-weight 400  
</style>  


